"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent } from "@/components/ui/card";
import { Flower2, Send, Mic } from "lucide-react";
import Link from "next/link";

interface Message {
  id: number;
  text: string;
  isUser: boolean;
  timestamp: Date;
}

export default function ChatPage() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: 1,
      text: "Hola, soy Alma, tu guía legal personal. Estoy aquí para ayudarte a entender tu situación y prepararte antes de contactar a un abogado. ¿Te parece bien si empezamos?",
      isUser: false,
      timestamp: new Date(),
    },
  ]);
  const [inputText, setInputText] = useState("");
  const [currentStep, setCurrentStep] = useState(0);

  const legalQuestions = [
    "Para comenzar, ¿qué tipo de situación estás atravesando?",
    "1. Divorcio\n2. Violencia familiar\n3. Tenencia de hijos\n4. Pensión de alimentos\n5. Otro",
    "Gracias. ¿Tu pareja y tú conviven actualmente?",
    "Lo siento mucho. Esto es importante para saber si necesitas medidas de protección adicionales.",
    "Te presentaré 3 abogados disponibles hoy, especializados en derecho de familia y violencia. ¿Deseas continuar con las recomendaciones?"
  ];

  const handleSendMessage = () => {
    if (!inputText.trim()) return;

    const userMessage: Message = {
      id: messages.length + 1,
      text: inputText,
      isUser: true,
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, userMessage]);
    setInputText("");

    // Simulate AI response
    setTimeout(() => {
      let aiResponse = "";
      
      if (currentStep < legalQuestions.length) {
        aiResponse = legalQuestions[currentStep];
        setCurrentStep(prev => prev + 1);
      } else {
        aiResponse = "Perfecto. He generado tu Case Brief. Un resumen por favor...";
      }

      const aiMessage: Message = {
        id: messages.length + 2,
        text: aiResponse,
        isUser: false,
        timestamp: new Date(),
      };

      setMessages(prev => [...prev, aiMessage]);

      // After the last question, redirect to case brief
      if (currentStep >= legalQuestions.length - 1) {
        setTimeout(() => {
          window.location.href = "/case-brief";
        }, 2000);
      }
    }, 1000);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSendMessage();
    }
  };

  return (
    <div className="min-h-screen bg-white flex flex-col">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 p-4 flex items-center space-x-3">
        <div className="flex items-center space-x-3">
          <Flower2 className="h-8 w-8 text-pink-500" />
          <div>
            <h1 className="font-semibold text-gray-900">Alma</h1>
            <p className="text-sm text-gray-500">Tu asistente legal</p>
          </div>
        </div>
      </div>

      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.map((message) => (
          <div
            key={message.id}
            className={`flex ${message.isUser ? 'justify-end' : 'justify-start'}`}
          >
            <div
              className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                message.isUser
                  ? 'bg-pink-500 text-white'
                  : 'bg-gray-100 text-gray-900'
              }`}
            >
              <p className="text-sm whitespace-pre-line">{message.text}</p>
              <p className={`text-xs mt-1 ${
                message.isUser ? 'text-pink-100' : 'text-gray-500'
              }`}>
                {message.timestamp.toLocaleTimeString([], { 
                  hour: '2-digit', 
                  minute: '2-digit' 
                })}
              </p>
            </div>
          </div>
        ))}
      </div>

      {/* Quick Response Buttons */}
      {currentStep === 1 && (
        <div className="p-4 space-y-2">
          <div className="grid grid-cols-2 gap-2">
            <Button 
              variant="outline" 
              size="sm"
              onClick={() => {
                setInputText("Divorcio");
                setTimeout(handleSendMessage, 100);
              }}
            >
              1. Divorcio
            </Button>
            <Button 
              variant="outline" 
              size="sm"
              onClick={() => {
                setInputText("Violencia familiar");
                setTimeout(handleSendMessage, 100);
              }}
            >
              2. Violencia familiar
            </Button>
            <Button 
              variant="outline" 
              size="sm"
              onClick={() => {
                setInputText("Tenencia de hijos");
                setTimeout(handleSendMessage, 100);
              }}
            >
              3. Tenencia de hijos
            </Button>
            <Button 
              variant="outline" 
              size="sm"
              onClick={() => {
                setInputText("Otro");
                setTimeout(handleSendMessage, 100);
              }}
            >
              5. Otro
            </Button>
          </div>
        </div>
      )}

      {currentStep === 2 && (
        <div className="p-4 space-y-2">
          <div className="flex space-x-2">
            <Button 
              variant="outline"
              onClick={() => {
                setInputText("Sí, por favor");
                setTimeout(handleSendMessage, 100);
              }}
            >
              Sí, por favor
            </Button>
            <Button 
              variant="outline"
              onClick={() => {
                setInputText("Sí, de todo tipo");
                setTimeout(handleSendMessage, 100);
              }}
            >
              Sí, de todo tipo
            </Button>
          </div>
        </div>
      )}

      {currentStep >= 4 && (
        <div className="p-4">
          <Button 
            className="w-full bg-pink-500 hover:bg-pink-600 text-white"
            onClick={() => {
              setInputText("Sí");
              setTimeout(handleSendMessage, 100);
            }}
          >
            Sí
          </Button>
        </div>
      )}

      {/* Input Area */}
      <div className="border-t border-gray-200 p-4">
        <div className="flex items-center space-x-2">
          <Input
            value={inputText}
            onChange={(e) => setInputText(e.target.value)}
            onKeyPress={handleKeyPress}
            placeholder="Escribe tu respuesta..."
            className="flex-1"
          />
          <Button
            size="icon"
            onClick={handleSendMessage}
            className="bg-pink-500 hover:bg-pink-600"
          >
            <Send className="h-4 w-4" />
          </Button>
          <Button
            size="icon"
            variant="outline"
          >
            <Mic className="h-4 w-4" />
          </Button>
        </div>
      </div>
    </div>
  );
}
